import React from "react";
import Image from "next/image";

import Link from "next/link";
import { Button } from "../ui/button";
import logo from "../../../public/assets/shape-17.png";

const MENULINKS = [
  { name: "Home", href: "/" },
  { name: "Courses", href: "/courses" },
  { name: "About", href: "/about" },
  { name: "Contact", href: "/contact" },
];

function NavBar() {
  return (
    <nav className=" h-[99px] flex items-center justify-between px-[132px] pt-5 pb-6">
      <div className=" h-[55px] flex items-center gap-[50px]">
        <div className=" w-[54px] h-[54px] rounded-lg bg-red-700 flex items-center justify-center">
          <Link href={"/"}>
            <Image src={logo} alt="logo" width={32.5} height={32.5} />
          </Link>
        </div>
        <div className="flex gap-[26px]">
          {MENULINKS.map((link) => (
            <Button
              key={link.href}
              className=" text-lg w-[100px] h-[55px] rounded-lg px-6 py-[14px]"
            >
              <Link href={link.href}> {link.name}</Link>
            </Button>
          ))}
        </div>
      </div>
      <div className=" h-[55px] flex items-center">
        <Tabs defaultValue="account" className="w-[400px]">
          <TabsList>
            <TabsTrigger value="account">Account</TabsTrigger>
            <TabsTrigger value="password">Password</TabsTrigger>
          </TabsList>
          <TabsContent value="account">
            Make changes to your account here.
          </TabsContent>
          <TabsContent value="password">Change your password here.</TabsContent>
        </Tabs>
        <Button>Sign Up</Button>
        <Button>Login</Button>
      </div>
    </nav>
  );
}

export default NavBar;
